import printValue from './printValue';

export const mixed = {
  default: 'O campo  invlido.',
  required: 'O campo  obrigatrio.',
  oneOf: 'O campo deve ter um dos seguintes valores: ${values}.',
  notOneOf: 'O campo no deve ter nenhum dos seguintes valores: ${values}.',
  notType: ({
    type, value, originalValue,
  }: any) => {
    const isCast = originalValue != null && originalValue !== value;
    let msg = `${`O campo deve ser do tipo \`${type}\`, `
      + `mas o valor final : \`${printValue(value, true)}\``}${
      isCast
        ? ` (cast do valor \`${printValue(originalValue, true)}\`).`
        : '.'}`;

    if (value === null) {
      msg += '\nSe a inteno era usar "null" como um valor em branco marque o esquema como `.nullable()`.';
    }

    return msg;
  },
  defined: 'O campo no deve ser indefinido.',
};

export const string = {
  length: ({ length }: any) => `O campo deve ter exatamente ${length} ${length === 1 ? 'caractere' : 'caracteres'}.`,
  min: ({ min }: any) => `O campo deve ter pelo menos ${min} ${min === 1 ? 'caractere' : 'caracteres'}.`,
  max: ({ max }: any) => `O campo deve ter no mximo ${max} ${max === 1 ? 'caractere' : 'caracteres'}.`,
  matches: 'O campo deve corresponder ao padro: "${regex}".',
  email: 'O campo deve ser um e-mail vlido.',
  url: 'O campo deve ser uma URL vlida.',
  trim: 'O campo no deve conter espaos adicionais no incio nem no fim.',
  lowercase: 'O campo deve estar em letras minsculas.',
  uppercase: 'O campo deve estar em letras maisculas.',
};

export const number = {
  min: 'O campo deve ser maior ou igual a ${min}.',
  max: 'O campo deve menor ou igual a ${max}.',
  lessThan: 'O campo deve ser menor que ${less}.',
  moreThan: 'O campo deve ser maior que ${more}.',
  notEqual: 'O campo no deve ser igual a ${notEqual}.',
  positive: 'O campo deve ser um nmero positivo.',
  negative: 'O campo deve ser um nmero negativo.',
  integer: 'O campo deve ser um nmero inteiro.',
};

export const date = {
  min: 'O campo deve ser posterior a ${min}.',
  max: 'O campo deve ser anterior a ${max}.',
};

export const boolean = {};

export const object = {
  noUnknown: 'O campo tem chaves desconhecidas: ${unknown}.',
};

export const array = {
  min: ({ min }: any) => `O campo deve ter pelo menos ${min} ${min === 1 ? 'item': 'itens'}.`,
  max: ({ max }: any) => `O campo deve ter no mximo ${max} ${max === 1 ? 'item': 'itens'}.`,
};

export default {
  mixed,
  string,
  number,
  date,
  object,
  array,
  boolean,
};
