import printValue from './printValue';

export const mixed = {
  default: '${path}  invlido',
  required: '${path}  obrigatrio',
  oneOf: '${path} deve ter um dos seguintes valores: ${values}',
  notOneOf: '${path} no deve ter nenhum dos seguintes valores: ${values}',
  notType: ({
    path, type, value, originalValue,
  }: any) => {
    const isCast = originalValue != null && originalValue !== value;
    let msg = `${`${path} deve ser do tipo \`${type}\`, `
      + `mas o valor final : \`${printValue(value, true)}\``}${
      isCast
        ? ` (cast do valor \`${printValue(originalValue, true)}\`)`
        : ''}`;

    if (value === null) {
      msg += '\nse a inteno era usar "null" como um valor em branco marque o esquema como `.nullable()`';
    }

    return msg;
  },
  defined: '${path} no deve ser indefinido',
};

export const string = {
  length: ({ path, length }: any) => `${path} deve ter exatamente ${length} ${length === 1 ? 'caractere' : 'caracteres'}`,
  min: ({ path, min }: any) => `${path} deve ter pelo menos ${min} ${min === 1 ? 'caractere' : 'caracteres'}`,
  max: ({ path, max }: any) => `${path} deve ter no mximo ${max} ${max === 1 ? 'caractere' : 'caracteres'}`,
  matches: '${path} deve corresponder ao padro: "${regex}"',
  email: '${path} deve ser um e-mail vlido',
  url: '${path} deve ser uma URL vlida',
  trim: '${path} no deve conter espaos adicionais no incio nem no fim',
  lowercase: '${path} deve estar em letras minsculas',
  uppercase: '${path} deve estar em letras maisculas',
};

export const number = {
  min: '${path} deve ser maior ou igual a ${min}',
  max: '${path} deve menor ou igual a ${max}',
  lessThan: '${path} deve ser menor que ${less}',
  moreThan: '${path} deve ser maior que ${more}',
  notEqual: '${path} no deve ser igual a ${notEqual}',
  positive: '${path} deve ser um nmero positivo',
  negative: '${path} deve ser um nmero negativo',
  integer: '${path} deve ser um nmero inteiro',
};

export const date = {
  min: '${path} deve ser posterior a ${min}',
  max: '${path} deve ser anterior a ${max}',
};

export const boolean = {};

export const object = {
  noUnknown: '${path} tem chaves desconhecidas: ${unknown}',
};

export const array = {
  min: ({ path, min }: any) => `${path} deve ter pelo menos ${min} ${min === 1 ? 'item' : 'itens'}`,
  max: ({ path, max }: any) => `${path} deve ter no mximo ${max} ${max === 1 ? 'item' : 'itens'}`,
};

export default {
  mixed,
  string,
  number,
  date,
  object,
  array,
  boolean,
};
