import printValue from './printValue';

export const mixed = {
  default: 'Invlido.',
  required: 'Obrigatrio.',
  oneOf: 'Deve ter um dos seguintes valores: ${values}.',
  notOneOf: 'No deve ter nenhum dos seguintes valores: ${values}.',
  notType: ({
    type, value, originalValue,
  }: any) => {
    const isCast = originalValue != null && originalValue !== value;
    let msg = `${`Deve ser do tipo \`${type}\`, `
      + `mas o valor final : \`${printValue(value, true)}\``}${
      isCast
        ? ` (cast do valor \`${printValue(originalValue, true)}\`).`
        : '.'}`;

    if (value === null) {
      msg += '\nSe a inteno era usar "null" como um valor em branco marque o esquema como `.nullable()`.';
    }

    return msg;
  },
  defined: 'No deve ser indefinido.',
};

export const string = {
  length: ({ length }: any) => `Deve ter exatamente ${length} ${length === 1 ? 'caractere' : 'caracteres'}.`,
  min: ({ min }: any) => `Deve ter pelo menos ${min} ${min === 1 ? 'caractere' : 'caracteres'}.`,
  max: ({ max }: any) => `Deve ter no mximo ${max} ${max === 1 ? 'caractere' : 'caracteres'}.`,
  matches: 'Deve corresponder ao padro: "${regex}".',
  email: 'Deve ser um e-mail vlido.',
  url: 'Deve ser uma URL vlida.',
  trim: 'No deve conter espaos adicionais no incio nem no fim.',
  lowercase: 'Deve estar em letras minsculas.',
  uppercase: 'Deve estar em letras maisculas.',
};

export const number = {
  min: 'Deve ser maior ou igual a ${min}.',
  max: 'Deve ser menor ou igual a ${max}.',
  lessThan: 'Deve ser menor que ${less}.',
  moreThan: 'Deve ser maior que ${more}.',
  notEqual: 'No deve ser igual a ${notEqual}.',
  positive: 'Deve ser um nmero positivo.',
  negative: 'Deve ser um nmero negativo.',
  integer: 'Deve ser um nmero inteiro.',
};

export const date = {
  min: 'Deve ser posterior a ${min}.',
  max: 'Deve ser anterior a ${max}.',
};

export const boolean = {};

export const object = {
  noUnknown: 'Existem chaves desconhecidas: ${unknown}.',
};

export const array = {
  min: ({ min } : any) => `Deve ter pelo menos ${min} ${min === 1 ? 'item': 'itens'}.`,
  max: ({ max } : any) => `Deve ter no mximo ${max} ${max === 1 ? 'item': 'itens'}.`,
};

export default {
  mixed,
  string,
  number,
  date,
  object,
  array,
  boolean,
};
